.virtualized-select {
  background-color: #2c303a;
  animation: fade_in 1s ease-in-out forwards;
  box-shadow: inset 0 0 5px #1e1f26, 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  color: white;
  padding: 20px;
  min-height: 115px;
  font-size: 1rem;
  border: 2px solid #1e1f26;
  border-radius: 2px;
  margin: 2vh 0;
  cursor: pointer;
  &:hover {
    border: 2px solid #ee760a;
  }

  & .Select-placeholder {
    color: white;
    margin-bottom: 15px;
    font-family: "Lato", sans-serif;
  }

  & input {
    width: 100% !important;
    margin: 0 auto;
    display: block;
    border: 1px solid white;
    border-radius: 1px;
    background: transparent;
    color: white;
    font-size: 1rem;
    padding: 0 2px;
    box-shadow: inset 0px 0px 2px 1px rgba(255, 255, 255, 0.54);
  }
  & .Select-menu-outer span:hover {
    background-color: white;
  }
}

.inactivatedCryptoCurrencySelectForm {
  opacity: 0.7;
  pointer-events: none;
}
